import { WorkItemProps } from '../global/props';

const API_URL = 'https://api.itcode.dev/klid/api/list';

/**
 * Î¶¨Ïä§Ìä∏ Î∞òÌôò API Î©îÏÑúÎìú
 *
 * @param {string | number} year: Ïó∞
 * @param {string | number} month: Ïõî
 *
 * @returns {Promise<WorkItemProps[]>} ÎπÑÎèôÍ∏∞ WorkItemProps[] Í∞ùÏ≤¥
 */
export async function getList(year: string | number, month: string | number): Promise<WorkItemProps[]>
{
	const response = await fetch(`${API_URL}/ym/${year}${month === '' ? '' : `/${month}`}`, { method: 'GET' });

	const { ok } = response;
	const json = await response.json();

	// Ï†ïÏÉÅ ÏùëÎãµÏùº Í≤ΩÏö∞
	if (ok)
	{
		return json.body;
	}

	// Ï†ïÏÉÅ ÏùëÎãµÏù¥ ÏïÑÎãê Í≤ΩÏö∞
	else
	{
		return [];
	}
}

/**
 * Ï£ºÎ≥Ñ Î¶¨Ïä§Ìä∏ Î∞òÌôò API Î©îÏÑúÎìú
 *
 * @param {string | number} year: Ïó∞
 * @param {string | number} week: Ï£º
 *
 * @returns {Promise<WorkItemProps[]>} ÎπÑÎèôÍ∏∞ WorkItemProps[] Í∞ùÏ≤¥
 */
export async function getWeekList(year: string | number, week: string | number): Promise<WorkItemProps[]>
{
	const response = await fetch(`${API_URL}/wk/${year}/${week}`, { method: 'GET' });

	const { ok } = response;
	const json = await response.json();

	// Ï†ïÏÉÅ ÏùëÎãµÏùº Í≤ΩÏö∞
	if (ok)
	{
		return json.body;
	}

	// Ï†ïÏÉÅ ÏùëÎãµÏù¥ ÏïÑÎãê Í≤ΩÏö∞
	else
	{
		return [];
	}
}

/**
 * Î¶¨Ïä§Ìä∏ Ï∂îÍ∞Ä ÏùëÎãµ Î∞òÌôò API Î©îÏÑúÎìú
 *
 * @param {WorkItemProps} item: Î¶¨Ïä§Ìä∏ ÏïÑÏù¥ÌÖú
 *
 * @returns {Promise<boolean>} ÎπÑÎèôÍ∏∞ boolean Í∞ùÏ≤¥
 */
export async function postList(item: WorkItemProps): Promise<boolean>
{
	const response = await fetch(`${API_URL}/item`, {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json'
		},
		body: JSON.stringify(item)
	});

	const { ok } = response;

	return ok;
}

export async function putList(item: WorkItemProps): Promise<string>
{
	const response = await fetch(`${API_URL}/item/${item.seq}`, {
		method: 'PUT',
		headers: {
			'Content-Type': 'application/json'
		},
		body: JSON.stringify(item)
	});

	const { ok } = response;
	const json = await response.json();

	// Ï†ïÏÉÅ ÏùëÎãµÏùº Í≤ΩÏö∞
	if (ok)
	{
		return json.body;
	}

	// Ï†ïÏÉÅ ÏùëÎãµÏù¥ ÏïÑÎãê Í≤ΩÏö∞
	else
	{
		return '';
	}
}

export async function deleteList(seq: number): Promise<boolean>
{
	const response = await fetch(`${API_URL}/item/${seq}`, { method: 'DELETE' });

	const { ok } = response;

	return ok;
}