import React, { ReactElement } from 'react';
import './Section.scss';

interface Props {
	url?: string,
	width?: number
	children: any
}

/**
 * ÏÑπÏÖò ReactElement Î∞òÌôò Î©îÏÑúÎìú
 *
 * @param {Props} props: Ïª¥Ìè¨ÎÑåÌä∏ ÌîÑÎ°úÌçºÌã∞
 *
 * @returns {ReactElement} Ïª¥Ìè¨ÎÑåÌä∏ ReactElement
 */
export default function Section({ url, width, children }: Props): ReactElement
{
	return (
		<section data-section={url} style={{ maxWidth: width ? width : 1024 }}>
			{children}
		</section>
	);
}